import React, { Component } from 'react';

class Filter extends Component {
    
    render() {
        const{onChange}=this.props
        return (
            <div className="mb-3">
                <input type="text"
                    className="form-control"
                   onChange={({target})=>onChange(target)}
               />
            </div>
        );
    }
}

export default Filter;
