import React, { Component,memo } from "react";
import "./Item.css";
import {Context} from "../context/context";

class Item extends Component {
  render() {
    console.log("render do");
   const { item} = this.props;
    return (
   <Context.Consumer>
     {({checkedFn,deleteFn})=>(
      <li className="item-box" key={"key-"+item.id}>
        <div className="form-check">
          <input
            className="form-check-input"
            type="checkbox"
            checked={item.packed}
            onChange={(e) => (
             
              checkedFn(e,item)
            )}
            id={item.id}
          />
          <label className="form-check-label" htmlFor={item.id}>
            {" "}
            {item.value}
          </label>
        </div>
        <button className="btn btn-secondary btn-sm" onClick={() => (deleteFn(item))}>
          Remove
        </button>
      </li>
    )}
      </Context.Consumer>
    );
  }
}

export default memo( Item);
