import React, { Component } from "react";
import NewItem from "./components/NewItem";
import ListItems from "./components/ListItems";
import { defaultState } from "./data";
import {Context} from "./context/context";

class App extends Component {
  state={
  items:[...defaultState],
   
  }

  checkedFn=(e,item)=>{
    if(e.target.checked){
      item.packed=true;
      this.setState({
        items:[...new Set([...this.state.items,item])],
      })
      
    }else
    item.packed=false;
    this.setState({
      items:[...new Set([...this.state.items,item])],
    })
    
  }
  deleteFn=(item)=>{
    let deleteItem=this.state.items.filter((i)=>i!==item)
this.setState({
items:[...deleteItem]
})
   
  }
  allUnpecked=()=>{
    let arr=this.state.items.map((item)=>{
item.packed=false;
    return item;
    }
    )
    this.setState({items:arr})
  }
  addFn=(state)=>{
    this.setState({
      items:[...this.state.items,state]
    })
     }
  

  render() {
 
    const{items}=this.state;
    let packedItems=[];
    let unpackedItems=[];
 items.map((item)=>{
   if(item.packed){
   return packedItems.push(item)
   }else{
    return unpackedItems.push(item);
   }
 })

 
    return (
      <div className="container py-3">
       <Context.Provider value={{checkedFn:this.checkedFn,deleteFn:this.deleteFn}}>
        <NewItem addFn={this.addFn}/>
        <div className="row">
          <div className="col-md-5">
            <ListItems key={"Unpacked Items"} title="Unpacked Items" items={unpackedItems} />
          </div>
          <div className="offset-md-2 col-md-5">
            <ListItems key={"Packed Items"} title="Packed Items" items={packedItems}   />
            <button className="btn btn-danger btn-lg btn-block" onClick={this.allUnpecked}>
              Mark All As Unpacked
            </button>
          </div>
        </div>
        </Context.Provider>
      </div>
    );
  }
}

export default App;
