import React, { Component } from "react";
import Item from "./Item";
import Filter from "./Filter";



class ListItems extends Component {
  state = {
 filter:"",

  };

  updateFilter =( target) => {
    if(target.value){   
   return this.setState({filter:target.value, flag:true})
  }
  return this.setState({filter:"",flag:false});
  }
  
  render() {
  const { title,items} = this.props;
  let renderItems=[];
  if(this.state.flag){
    renderItems=items.filter((item)=>item.value.toLowerCase().includes(this.state.filter.toLowerCase())).map((item)=>( <Item item={item} key={item.id} />));
  }else{
     renderItems=items.map((item)=>( <Item item={item} key={item.id} />));
}
  return (
      <section>
        <h3 className="mb-3">{title}</h3>
        <Filter filter={title} onChange={this.updateFilter} />
        <ul className="mb-3 p-0">
       {renderItems}
        </ul>
      </section>
    );
  }
  
}

export default  ListItems;
